import { Card } from '../view/CardView';
/** Represents a cart item card with product information */
export class CartItemCard extends Card {
    constructor(container, events, domService, actions) {
        const template = document.querySelector('#card-basket');
        if (!template) {
            throw new Error('Card basket template not found');
        }
        super(template, events, domService);
        this.events = events;
        this.domService = domService;
        this.actions = actions;
        this.data = null;
        this.SELECTORS = {
            title: 'h3',
            price: 'span',
            index: 'div',
        };
        this.createElements();
        this.domService.appendChild(container, this._container);
        if (actions === null || actions === void 0 ? void 0 : actions.onClick) {
            this._container.addEventListener('click', actions.onClick);
        }
    }
    /** Creates and initializes all DOM elements for the cart item */
    createElements() {
        this._container = this.domService.createElement('div', [
            'basket__item',
            'card',
            'card_compact',
        ]);
        const elements = {
            index: this.domService.createElement('div', 'basket__item-index'),
            title: this.domService.createElement('h3', 'card__title'),
            price: this.domService.createElement('span', 'card__price'),
            deleteButton: this.domService.createElement('button', 'basket__item-delete'),
        };
        Object.values(elements).forEach((element) => this.domService.appendChild(this._container, element));
        elements.deleteButton.addEventListener('click', (e) => {
            e.preventDefault();
            this.events.emit('card:delete', this.data);
        });
    }
    set title(value) {
        const titleElement = this._container.querySelector(this.SELECTORS.title);
        if (titleElement) {
            titleElement.textContent = value;
        }
    }
    set price(value) {
        const priceElement = this._container.querySelector(this.SELECTORS.price);
        if (priceElement) {
            priceElement.textContent = value ? `${value} ` : '';
        }
    }
    set index(value) {
        const indexElement = this._container.querySelector(this.SELECTORS.index);
        if (indexElement) {
            indexElement.textContent = value.toString();
        }
    }
    set product(value) {
        this.data = value;
    }
    /** Sets the product data and updates the view */
    render(data) {
        this.product = data;
        this.title = data.title;
        this.price = data.price;
        return this._container;
    }
    /**
     * Updates the index display of the cart item
     * @param index - The position of the item in the cart
     */
    setIndex(index) {
        this.index = index;
    }
}
