/** View component for handling contact form display and interactions */
export class ContactsView {
    /**
     * Creates a new ContactsView instance
     * @param template - Contact form template
     * @param events - Event emitter instance
     * @param domService - DOM manipulation service
     */
    constructor(template, events, domService) {
        this.events = events;
        this.domService = domService;
        this._form = template.content
            .querySelector('.form')
            .cloneNode(true);
        this._button = this._form.querySelector('button[type="submit"]');
        this._errors = this._form.querySelector('.form__errors');
        this._inputs = Array.from(this._form.querySelectorAll('.form__input'));
        this._inputs.forEach((input) => {
            input.addEventListener('input', (e) => {
                this.events.emit('contacts:changeInput', {
                    field: e.target.name,
                    value: e.target.value,
                });
            });
        });
        this._form.addEventListener('submit', (e) => {
            e.preventDefault();
            this.events.emit('contacts:submit');
        });
        // Add phone input handling
        const phoneInput = this._form.querySelector('input[name="phone"]');
        phoneInput.value = '+7';
        phoneInput.addEventListener('input', this.handlePhoneInput.bind(this));
        phoneInput.addEventListener('keydown', (e) => {
            if (e.key === 'Backspace' &&
                e.target.selectionStart <= 2) {
                e.preventDefault();
            }
        });
    }
    /**
     * Handles phone input formatting and validation
     * @param e - Input event
     */
    handlePhoneInput(e) {
        const input = e.target;
        let value = input.value.replace(/\D/g, '');
        if (value.length > 11) {
            value = value.slice(0, 11);
        }
        let formatted = '+7';
        if (value.length > 1)
            formatted += ` ${value.slice(1, 4)}`;
        if (value.length > 4)
            formatted += ` ${value.slice(4, 7)}`;
        if (value.length > 7)
            formatted += ` ${value.slice(7, 9)}`;
        if (value.length > 9)
            formatted += ` ${value.slice(9)}`;
        input.value = formatted;
        this.events.emit('contacts:changeInput', {
            field: input.name,
            value: formatted,
        });
    }
    set error(value) {
        this.domService.setContent(this._errors, value);
    }
    set valid(value) {
        if (value) {
            this._button.removeAttribute('disabled');
        }
        else {
            this._button.setAttribute('disabled', 'disabled');
        }
    }
    render() {
        return this._form;
    }
    resetForm() {
        this._form.reset();
        this.error = '';
    }
}
