import { BasePresenter } from '../base/presenter';
import { ApiError } from '../../services/api/BaseApi';
/** Handles product display and interactions */
export class ProductPresenter extends BasePresenter {
    constructor(stateService, view, api, events) {
        super(events);
        this.stateService = stateService;
        this.view = view;
        this.api = api;
        this.gallery = document.querySelector('.gallery');
        this.events.on('state:products:changed', this.handleProductsReceived.bind(this));
        this.events.on('state:cart:changed', this.updateGalleryState.bind(this));
        this.events.on('card:select', (product) => {
            this.stateService.openModal(this.view.renderModal(product), product.title);
        });
        this.events.on('card:addCart', (product) => {
            this.stateService.addToCart(product);
        });
    }
    updateGalleryState() {
        const cart = this.stateService.getCart();
        this.stateService.getProducts().forEach((product) => {
            const cardElement = this.gallery.querySelector(`[data-product-id="${product.id}"]`);
            if (cardElement) {
                const cardInstance = cardElement.__cardInstance;
                if (cardInstance === null || cardInstance === void 0 ? void 0 : cardInstance.updateButtonState) {
                    const isInCart = cart.items.some((item) => item.id === product.id);
                    cardInstance.updateButtonState(isInCart, product.price !== undefined);
                }
            }
        });
    }
    renderProductCard(product) {
        const cardElement = this.view.render(product);
        cardElement.setAttribute('data-product-id', product.id);
        this.gallery.appendChild(cardElement);
    }
    async init() {
        try {
            this.stateService.setLoading('products', true);
            const products = await this.api.getListProductCard();
            this.stateService.setProducts(products);
        }
        catch (error) {
            console.error('Failed to load products:', error);
            if (error instanceof ApiError) {
                switch (error.type) {
                    case 'network':
                        this.stateService.setError('Network connection error. Please check your internet connection.');
                        break;
                    case 'server':
                        this.stateService.setError('Server error. Please try again later.');
                        break;
                    default:
                        this.stateService.setError(error.message);
                }
            }
            else {
                this.stateService.setError('Failed to load products');
            }
        }
        finally {
            this.stateService.setLoading('products', false);
        }
    }
    handleProductsReceived() {
        if (!this.gallery) {
            console.error('Gallery element not found');
            return;
        }
        try {
            const products = this.stateService.getProducts();
            this.gallery.innerHTML = '';
            products.forEach(this.renderProductCard.bind(this));
        }
        catch (error) {
            console.error('Error rendering products:', error);
        }
    }
}
