import { BaseApi } from './BaseApi';
import { API_CONFIG } from '../../config/api.config';
/** Handles product-related API requests */
export class ProductApi extends BaseApi {
    constructor(cdn, baseUrl, options) {
        super(baseUrl, options);
        this.cdn = cdn;
    }
    /**
     * Gets list of products from the server
     * @throws {ApiError} When products cannot be fetched
     */
    async getListProductCard() {
        const data = await this.get(API_CONFIG.ENDPOINTS.products);
        return data.items.map((item) => (Object.assign(Object.assign({}, item), { image: item.image.startsWith('http') ? item.image : this.cdn + item.image })));
    }
    /**
     * Orders products from the server
     * @throws {ApiError} When order cannot be processed
     */
    async orderProducts(order) {
        return this.post(API_CONFIG.ENDPOINTS.orders, order);
    }
}
