export class Order {
    constructor(template, events, domService) {
        this.template = template;
        this.events = events;
        this.domService = domService;
        this._form = template.content
            .querySelector('.form')
            .cloneNode(true);
        this._paymentButtons = Array.from(this._form.querySelectorAll('button[name="card"], button[name="cash"]'));
        this._address = this._form.querySelector('input[name="address"]');
        this._button = this._form.querySelector('.order__button');
        this._error = this._form.querySelector('.form__errors');
        this._paymentButtons.forEach((button) => {
            button.addEventListener('click', () => {
                this.handlePayment(button);
            });
        });
        this._address.addEventListener('input', (e) => {
            this.handleInput(e);
        });
        this._button.addEventListener('click', (e) => {
            e.preventDefault();
            this.events.emit('order:submit');
        });
    }
    render() {
        return this._form;
    }
    set valid(value) {
        if (value) {
            this._button.removeAttribute('disabled');
        }
        else {
            this._button.setAttribute('disabled', 'disabled');
        }
    }
    set error(value) {
        this.domService.setContent(this._error, value);
    }
    resetForm() {
        this._address.value = '';
        this._paymentButtons.forEach((button) => {
            this.domService.removeClass(button, 'button_alt-active');
        });
    }
    handlePayment(button) {
        this._paymentButtons.forEach((btn) => {
            this.domService.removeClass(btn, 'button_alt-active');
        });
        this.domService.addClass(button, 'button_alt-active');
        this.events.emit('order:paymentSelection', { payment: button.name });
    }
    handleInput(e) {
        this.events.emit('order:changeAddress', {
            field: e.target.name,
            value: e.target.value,
        });
    }
}
